import { Component } from '@angular/core';
//import { Address } from '../address/address';
import { NavController, NavParams, IonicPage } from 'ionic-angular';
import { Values } from '../../../providers/service/values';
import {Service} from '../../../providers/service/service';
import { Functions } from '../../../providers/service/functions';

@IonicPage()
@Component({
  templateUrl: 'edit-address-form.html',
})
export class EditAddressForm {

  id: any;
  zone: any;
  href: any;
  form: any;
  status: any;
  address: any;
  newAddress: any;
  buttonText: any;
  regions: any = [];
  public disableSubmit: boolean = false;
  
  constructor(public nav: NavController, public service: Service, params: NavParams, public functions: Functions, public values: Values) {

    this.id = params.data;
    this.address = {};
    this.newAddress = {};
    this.buttonText = this.values.lan.text_button_continue;  

    this.service.editAddressForm(this.id)
    .then((results) => this.a(results));  
  }

 
  getZone(id) {
    this.form.zone_id = null;
    this.service.getZone(id)
    .then((results) => this.zone = results);
  }

  
  saveAddress(href){
    if(this.validateForm()){
      this.disableSubmit= true;
      this.buttonText= this.form.text_loading; 
      this.service.saveAddress(this.form, href)
      .then((results) => this.handleResults(results));
    }   
  }


  handleResults(results){
    this.disableSubmit = false;
    this.buttonText= this.form.button_continue; 
    if(results.success != undefined){ 
      this.nav.setRoot('Address');      
    }

    else{this.form = results;}
  } 
  

  a(b){
    this.form = b;
    this.getZone(this.form.country_id);
  }

 
  encodeUrl(href) {
    return href.replace(/&amp;/g, '&');
  }
  

  validateForm(){
    if(this.form.country_id == undefined){this.functions.showAlert(this.values.lan.text_error, this.values.lan.error_country); return false}
      if(this.form.zone_id == undefined || this.form.zone_id == null){
        this.form.zone_id = "0";
      }
    return true
  } 

   
}



